*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}

.center{
  max-width:1160px;
  margin:0 auto;
  padding:0 2%;
  display:flex;
  flex-wrap:wrap;
}
.main{
  padding:40px 0;
  width:100%;
  background-color:lightblue;
  min-height:400px;
}
.menu{
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content:space-between;
}
.menu h3{
  color:white;
}
.menu a{
  color:white;
  text-decoration:none;
  border:1px solid white;
  padding:3px 8px;
  border-radius:5px;
  cursor:pointer;
  font-size:23px;
}
.menu a:hover{
  background-color:rgb(136, 182, 197);
  color: rgb(19, 17, 17);
  border-color:rgb(60, 97, 109);
}

.form{
  display:flex;
  flex-wrap:wrap;
  width:100%;
  max-width:600px;
  margin:10px auto;
  justify-content:center;
}

.items-form{
  margin:0 auto;
  max-width:540px;
  display:flex;
  width:100%;
  justify-content:space-around;
  flex-wrap:wrap;
}
.items-form input{
  width:44%;
  height:30px;
  margin:8px 0px;
}
.chamada{
  width:100%;
  display:flex;
  flex-wrap:wrap;
  padding:40px;
}
.chamada-items{
  width:50%;
  padding:20px;
}
@media screen and (max-width:768px){
  .chamada-items{
    width:100%;
  }
}